import React from 'react';
import 'antd/dist/antd.css';
import {Button,Icon,Spin} from 'antd';
import {Result} from './Layout';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import './Layout.css'


class Canvas extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            style:{
                position:"absolute",
                left:0,
                top:0,
                background:"red",
            },
            isDraw:false
        })
        this.canvas = React.createRef();
    }

    componentDidUpdate(){
        let point = this.props.point;    
        let x1 = point[0].split(',')[0];
        let y1 = point[0].split(',')[1];
        let x2 = point[1].split(',')[0];
        let y2 = point[1].split(',')[1];
        let x3 = point[2].split(',')[0];
        let y3 = point[2].split(',')[1];
        let x4 = point[3].split(',')[0];
        let y4 = point[3].split(',')[1];

        let leftMin = Math.min(x1, x2, x3, x4);
        let rightMax = Math.max(x1, x2, x3, x4);
        let topMin = Math.min(y1, y2, y3, y4);
        let bottomMax = Math.max(y1, y2, y3, y4);    
        
        let _x1 = x1 - leftMin;
        let _x2 = x2 - leftMin;
        let _x3 = x3 - leftMin;
        let _x4 = x4 - leftMin;
        let _y1 = y1 - topMin;
        let _y2 = y2 - topMin;
        let _y3 = y3 - topMin;
        let _y4 = y4 - topMin;


        var canvas = this.canvas.current;
        //console.log(_x1,_y1,_x2,_y2,_x3,_y3,_x4,_y4);
        if(canvas.getContext){
            //debugger;
            let ctx = canvas.getContext("2d");
            ctx.fillStyle = 'rgba(254,180,32,0.5)';
            ctx.clearRect(0,0,canvas.width,canvas.height);
            ctx.beginPath();
            ctx.moveTo(_x1, _y1);
            ctx.lineTo(_x2, _y2);
            ctx.lineTo(_x3, _y3);
            ctx.lineTo(_x4, _y4);
            ctx.fill();
        }
        
    }

    render(){
        let point = this.props.point;    
        let x1 = point[0].split(',')[0];
        let y1 = point[0].split(',')[1];
        let x2 = point[1].split(',')[0];
        let y2 = point[1].split(',')[1];
        let x3 = point[2].split(',')[0];
        let y3 = point[2].split(',')[1];
        let x4 = point[3].split(',')[0];
        let y4 = point[3].split(',')[1];

        let leftMin = Math.min(x1, x2, x3, x4);
        let rightMax = Math.max(x1, x2, x3, x4);
        let topMin = Math.min(y1, y2, y3, y4);
        let bottomMax = Math.max(y1, y2, y3, y4);    
        let cvsStyle={
            position:"absolute",
            left:leftMin+"px",
            top:topMin+"px",
            //background:"#80008021",
        }
        return(
            <>
            {
                <canvas
                ref={this.canvas}
                width={rightMax-leftMin} height={bottomMax-topMin} style={cvsStyle}>
                </canvas>
            }
            </>
            
        )
    }
}

const Dynamsoft = window.Dynamsoft;
let scanner = null;
class Scanner extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            numbers:[],
            resultsInfo:[],
            isOpen:false,
            resultsPoint:[],
            camera:0
        })
    }

    showScanner(){
        // var fullWidth = $(".wrap-container").width();
        // var fullHeight = $(".wrap-container").height();
        // var frameWidth= $(".frame").width();
        // var frameHeight = $(".frame").height();
    
    
        // function switchScannerArea(scanner,isFullRegion){
        //     let settings = scanner.getRuntimeSettings();
        //     console.log(settings.region);
        //     if(isFullRegion===false){
        //         settings.region.left = ((fullWidth-frameWidth)/2)/fullWidth*100;
        //         settings.region.top = ((fullHeight-frameHeight)/2)/fullHeight*100;
        //         settings.region.right = (frameWidth + (fullWidth-frameWidth)/2)/fullWidth*100;
        //         settings.region.bottom = (frameHeight + (fullHeight-frameHeight)/2)/fullHeight*100;
        //         settings.region.measuredByPercentage = 1; // 1 means true
        //         console.log(settings.region.left,settings.region.top);
        //     }
        //     else{
        //         settings.region.left = 0;
        //         settings.region.top = 0;
        //         settings.region.right = 0;
        //         settings.region.bottom = 0;
        //         settings.region.measuredByPercentage = 1; // 1 means true
        //     }
        //     scanner.updateRuntimeSettings(settings);
        // }
    
        
        Dynamsoft.BarcodeScanner.createInstance({
            intervalTime:200,
            UIElement:document.getElementById('scanner'),
            videoSettings: { video: { width: { ideal: 1280 }, height: {ideal: 720 }, facingMode: { ideal: 'environment' } } },
            onFrameRead: results => {
                let resultPointsPerFrame=[];
                for (let i = 0; i < results.length; i++){
                    let result = results[i];
                    resultPointsPerFrame.push(result.LocalizationResult.ResultPoints);
                }

                let resultsInfo = scanner.arrDiffCodeInfo;
                this.setState({
                    resultsInfo:resultsInfo,
                    resultsPoint:resultPointsPerFrame,
                });
                
                console.log(this.state.resultsInfo);
            },
            onUnduplicatedRead: (txt, result) => {
                console.log(result,typeof result);
            }
        }).then(s => {
            scanner = s;
        }).then(()=>{
            if(scanner._isOpen===false){
                console.log("open!");
                scanner.show().then(()=>
                    scanner.getAllCameras().then(infos=>{
                        scanner.setCurrentCamera(infos[this.state.camera].deviceId);
                    })
                )
            }
            else{
                console.log("close!");
                scanner.show().then(()=>scanner.close());
            }
        }).then(()=>{
            this.setState({
                isOpen:!this.state.isOpen
            })
        })
    }

    componentDidMount(){
        this.showScanner();
    }

    componentWillUnmount(){
        this.showScanner();
    }

    onSwitchCamera(){
        if(this.state.camera===0){
            this.setState({
                camera:1
            })
        }
        else{
            this.setState({
                camera:0
            })
        }
        scanner.getAllCameras().then(infos=>{
            scanner.setCurrentCamera(infos[this.state.camera].deviceId);
        })
    }

    render(){
        const allCanvas = this.state.resultsPoint.map((eachResult,index)=>
            <Canvas key={index} point={eachResult}></Canvas>
        );
        return(
            <>
            <style type="text/css">
                {`
                .waiting{
                    position:absolute;
                    left:50%;
                    top:50%;
                    transform:translate(-50%);
                    color:#FE8E14;
                    transition: opacity 1000ms ease-in;
                }

                .fade-enter.fade-enter-active {
                    opacity:1;
                    transition: opacity 1000ms ease-in;
                }

                .fade-enter{
                    opacity:0;
                }

                .fade-leave{
                    opacity: 1; 
                }

                .fade-leave.fade-leave-active{
                    opacity:0;
                    transition: opacity 3000ms ease-in;
                }
                
                .fade-appear{
                    opacity:0;
                }

                .fade-appear.fade-appear-active {
                    opacity: 1;
                    transition: opacity 2000ms ease-in;
                }
                `}
            </style>
            <ReactCSSTransitionGroup
                transitionName="fade"
                transitionLeave={true}
                transitionAppear={false}
                transitionEnter={false}
                transitionAppearTimeout={500}
                transitionLeaveTimeout={3500}
                transitionEnterTimeout={2500}
            >
                {
                    !this.state.isOpen&&
                    <Spin 
                    className="waiting" 
                    tip="Accessing Camera list..." 
                    indicator={<Icon type="smile" spin style={{ fontSize: "3rem" ,}}></Icon>}>
                    </Spin>    
                }
            </ReactCSSTransitionGroup>           
            
            <div id='scanner' style={{position:"absolute"}}>
                <video className='dbrScanner-video' playsInline={true}></video>
                {
                    this.state.resultsPoint.length&&
                    <>{allCanvas}</>
                }
            </div>
            <Result resultsInfo={this.state.resultsInfo}></Result>
            <button onClick={this.onSwitchCamera.bind(this)} style={{position:"absolute",top:"60px",left:0}}>切换相机</button>
            {/* <div style={{"position":"absolute",bottom:"130px"}}>
                <Button type="primary" onClick={this.showScanner.bind(this)}>{this.state.isOpen?"close":"open"} scanner</Button>
            </div> */}
            </>
        )
    }
}

export default Scanner;