import React from 'react';
import "./Layout.css";
import {Button,Icon,notification,message} from 'antd';
import SettingPage from './SettingPage';
import FilePage from './FilePage';
import Scanner from "./Scanner";

function RegionBtn(props){
    return(
        <label className="region" onClick={props.handleRegion}>
            <Icon type={props.isFullRegion?"fullscreen-exit":"fullscreen"} style={{fontSize:"2rem",color:"#FE8E14"}}></Icon>
        </label>
    )
}

function ScannerArea(){
    return(
        <div className="scanner-container">
            <div className="frame">
                <label className="flashlight">
                    <Icon type="funnel-plot" style={{fontSize:"2rem",color:"#FE8E14"}}></Icon>
                </label>
            </div>
            <div className="tip-info">
                <p>Put the code in the box and scan it automatically.</p>
            </div>
        </div>
    )
}



class Body extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            isFullRegion:false,
        })
    }
    handleRegion(){
        this.setState({
            isFullRegion:!this.state.isFullRegion,
        })
    }
    render(){
        return (
            <div className="home-screen">
                <Scanner></Scanner>
                {
                    !this.state.isFullRegion&&
                    <ScannerArea></ScannerArea>
                }
                <div className="dynam-info">
                    {/* <p>Dynamsoft DBR</p> */}
                    <a href="https://www.dynamsoft.com" ><img src="img/logo-dynamsoft-blackBg-190x47.png" alt="logo" style={{width:"50%"}}></img></a>
                </div>
                
                <RegionBtn handleRegion={this.handleRegion.bind(this)} isFullRegion={this.state.isFullRegion}></RegionBtn>
            </div>
        )
    }
}

let clicks = [];
let timeout;

class Head extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            isShowSettingPage:false,
            isShowFilePage:false,
            isFullScreen:false,
        })
    }

    handleShowSettingPage(){
        this.setState({
            isShowSettingPage:!this.state.isShowSettingPage,
        });
    }

    handleShowFilePage(){
        this.setState({
            isShowFilePage:!this.state.isShowFilePage,
        });
    }

    switchFullScreen(){
        //console.log("fullscreen suceess!");
        if(!this.state.isFullScreen){
            if(document.documentElement.requestFullscreen){
                document.documentElement.requestFullscreen();
            }
            else if(document.documentElement.webkitRequestFullScreen){
                document.documentElement.webkitRequestFullScreen();
            }
            else if(document.documentElement.mozRequestFullScreen){
                document.documentElement.mozRequestFullScreen();
            }
            else{
                document.documentElement.msRequestFullscreen();
            }
            //document.documentElement.requestFullscreen(); 
        }
            
        else
            document.exitFullscreen();
        this.setState({
            isFullScreen:!this.state.isFullScreen,
        })       
    }

    fullSceenClickHandler(event){
        event.preventDefault();
        clicks.push(new Date().getTime());
        window.clearTimeout(timeout);
        timeout = window.setTimeout(()=>{
            if (clicks.length > 1 && clicks[clicks.length - 1] - clicks[clicks.length - 2] < 250){
                this.switchFullScreen();
            }
        },250);
    }   

    render(){
        if(this.state.isShowSettingPage){
            return(
                <SettingPage onBackClick={this.handleShowSettingPage.bind(this)}></SettingPage>
            )
        }

        if(this.state.isShowFilePage){
            return(
                <FilePage onBackClick={this.handleShowFilePage.bind(this)}></FilePage>
            )
        }
        else{
            return(
                <div className="header-bar">
                    <div className="settingBtn-container" >
                        <Icon type="setting" style={{fontSize:"2.5rem",color:"#FE8E14"}} onClick={this.handleShowSettingPage.bind(this)} ></Icon>
                    </div>
                    <div className="double-click" >
                        <label onClick={this.fullSceenClickHandler.bind(this)}>双击{this.state.isFullScreen&&"退出"}全屏</label>
                    </div>
                    <div className="selImgBtn-container">
                        <Icon type="plus" style={{fontSize:"2.5rem",color:"#FE8E14"}} onClick={this.handleShowFilePage.bind(this)}></Icon>
                    </div>
                </div>
            )
        }
    }
    
}


class EachResult extends React.Component{
    constructor(props){
        super(props);
    }

    copyScannerResult(){
        const kUtil=window.kUtil;
        kUtil.copyToClipBoard(this.props.content);
        // message.config({
        //     top:window.innerHeight-180,
        //     maxCount:3,
        //     
        // })
        //message.success("copy successfully!");
        var config={};
        config.content="copy successfully!";
        config.icon=<Icon type="smile" style={{color:"#FE8E14"}}></Icon>;
        message.config({
            top:window.innerHeight-180,
            duration:1.5,
        })
        message.open(config);
    }

    render(){
        const copyNotice = () => {
            this.copyScannerResult();
            notification.open({
                message: 'Notice:',
                placement:"bottomLeft",
                duration:1.5,
                description:
                '复制成功!',
                icon:<Icon type="smile" style={{color:"#FE8E14"}}></Icon>,
                style:{
                    width:200,
                    marginLeft:335-200
                }
            });
        };

        let txt = this.props.content;
        let possibleLink = txt;
        if (!txt.startsWith('http') && (txt.startsWith('www') || -1 !== txt.indexOf('.com') ||
            -1 !== txt.indexOf('.net') || -1 !== txt.indexOf('.org') || -1 !== txt.indexOf('.edu'))) {
            possibleLink = 'http://' + txt;
        }
        let isLink = possibleLink.startsWith('http');
        return(
            <div className="result-content">
                <div>
                    <><span style={{color:"#FE8E14"}}>{this.props.format}: </span></>
                    {
                        isLink?
                        <a href={possibleLink} target={"_blank"} style={{textDecoration:"underline"}} >{this.props.content}</a>
                        : <span onClick={this.copyScannerResult.bind(this)}>{this.props.content}</span> 
                    }
                    <><span style={{color:"#FE8E14"}}> x {this.props.count}</span></>
                    {/* <Button type="link" icon="copy" size="small" style={{float:"right"}}  onClick={this.copyScannerResult.bind(this)}></Button> */}
                </div>
            </div>
            )
        }
}


class Result extends React.Component{
    constructor(props){
        super(props);
    }
    
    render(){
        const resultItems = this.props.resultsInfo.slice(-3).map((ri,index)=>
            <EachResult key={index} content = {ri.result.BarcodeText} count={ri.count} format={ri.result.BarcodeFormatString}></EachResult>
        );
        return(
            <div className="result-container">
                {resultItems}
            </div>
        )
    }
}

class Layout extends React.Component{
    constructor(props){
        super(props);
        this.state=({
            isOpen:false,
            isShow:true,
        })
    }

    handleShow(){
        this.setState({
            isShow:!this.state.isShow,
        });
        console.log(this.state.isShow);
    }

    render(){
        return (
            <div className="wrap-container">
                {
                    this.state.isShow&&<Body></Body>
                }
                <Head onSwitchPage={this.handleShow.bind(this)}></Head>
            </div>
        )
    }
}

export default Layout;
export {Result};